import React, { useState, useContext } from 'react';
import { Button, Form } from 'react-bootstrap';
import { UserContext } from '../contexts/UserContext';
import { Redirect } from 'react-router-dom';
import Loader from 'react-loader';

function Login() {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");

    const { userToken, setUserToken, setUserTopicId, setUserCompanyId } = useContext(UserContext);
    const [isLoaded, setIsLoaded] = useState(true);

    const handleSubmit = e => {
        e.preventDefault();
        setIsLoaded(false);
        fetch(`${process.env.REACT_APP_API}/auth/local`, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                identifier: email,
                password: password
            })
        }).then(res => res.json()).then(data => {
            if (data.jwt) {
                localStorage.setItem('matesfeed-user', data.jwt);
                localStorage.setItem('matesfeed-user-topic-id', data.user.topic_id ?? -1);
                localStorage.setItem('matesfeed-user-company-id', data.user.company_id ?? -1);

                setUserCompanyId(data.user.company_id ?? -1);
                setUserTopicId(data.user.topic_id ?? -1);
                setUserToken(data.jwt);
            }
        }).catch(err => {
            setIsLoaded(true);
            console.log(err);
            alert("Oops! something went wrong :(");
        })

    }

    if (userToken !== "null") return <Redirect to="/" />;
    return (
        <Loader loaded={isLoaded}>
            <div className="container mt-4">
                <Form onSubmit={handleSubmit}>
                    <h4>Login Form</h4>
                    <p>This form is only for contributors.</p>
                    <hr />
                    <Form.Group>
                        <Form.Label>Email</Form.Label>
                        <Form.Control type="email" placeholder="username@matesfeed.com" required onChange={e => setEmail(e.target.value)} />
                    </Form.Group>

                    <Form.Group>
                        <Form.Label>Password</Form.Label>
                        <Form.Control type="password" placeholder="enter your password here" required onChange={e => setPassword(e.target.value)} />
                    </Form.Group>

                    <Form.Text className="text-muted">
                        ** If you forgot or want to reset your password please contact admins.
                </Form.Text>

                    <Button variant="primary" type="submit">Login</Button>
                </Form>
            </div>
        </Loader>
    );
}

export default Login;