import React, { useContext, useEffect, useState } from 'react';
import { Nav } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import Extras from './Extras';
import Notes from './Notes';
import Problems from './Problems';
import { UserContext } from '../../contexts/UserContext';
import Loader from 'react-loader';

function Subtopic(props) {
    const [subtopic, setSubtopic] = useState({});
    const { userToken } = useContext(UserContext);
    const [isLoaded, setIsLoaded] = useState(true);

    useEffect(() => {
        if (userToken !== "null") {
            setIsLoaded(false);
            fetch(`${process.env.REACT_APP_API}/subtopics/${props.match.params.id}`)
                .then(res => res.json())
                .then(data => {
                    setSubtopic(data);
                    setIsLoaded(true);
                })
                .catch(err => {
                    setSubtopic([]);
                    setIsLoaded(true);
                    alert("Oops! something went wrong :(")
                })
        }
    }, []);

    return (
        <Loader loaded={isLoaded}>
            <div>
                <Nav fill variant="pills" activeKey={props.match.params.cat} className="py-3">
                    <Nav.Item><Nav.Link eventKey={"notes"} as={Link} to={`/topics/${props.match.params.topic_id}/subtopics/${props.match.params.id}/notes`}>Notes</Nav.Link></Nav.Item>
                    <Nav.Item><Nav.Link eventKey={"problems"} as={Link} to={`/topics/${props.match.params.topic_id}/subtopics/${props.match.params.id}/problems`}>Problems</Nav.Link></Nav.Item>
                    <Nav.Item><Nav.Link eventKey={"extras"} as={Link} to={`/topics/${props.match.params.topic_id}/subtopics/${props.match.params.id}/extras`}>Extras</Nav.Link></Nav.Item>
                </Nav>
                <hr className="mt-0" />
                {props.match.params.cat === "notes" ? <Notes subtopic={subtopic} topic_id={props.match.params.topic_id} id={props.match.params.id} /> : null}
                {props.match.params.cat === "problems" ? <Problems subtopic={subtopic} topic_id={props.match.params.topic_id} id={props.match.params.id} /> : null}
                {props.match.params.cat === "extras" ? <Extras subtopic={subtopic} topic_id={props.match.params.topic_id} id={props.match.params.id} /> : null}
            </div>
        </Loader>
    );
}

export default Subtopic;