import React, { useState } from 'react';

const UserContext = React.createContext();

function UserContextProvider(props) {

    const [userToken, setUserToken] = useState("null");
    const [userTopicId, setUserTopicId] = useState("-1");
    const [userCompanyId, setUserCompanyId] = useState("-1");

    return <UserContext.Provider value={{ userToken, setUserToken, userTopicId, setUserTopicId, userCompanyId, setUserCompanyId }}>{props.children}</UserContext.Provider>
}

export default UserContextProvider;
export { UserContext };