import React, { useContext, useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { UserContext } from '../../contexts/UserContext';
import FA from 'react-fontawesome';
import { Modal, Form, Alert, Button } from 'react-bootstrap';
import Loader from 'react-loader';

function SubtopicsList(props) {
    const topic_id = parseInt(props.match.params.topic_id);

    const [subtopics, setSubtopics] = useState([]);
    const { userToken, userTopicId } = useContext(UserContext);

    const [showModal, setShowModal] = useState(false);
    const [isLoaded, setIsLoaded] = useState(true);

    useEffect(() => {
        setIsLoaded(false);
        fetch(`${process.env.REACT_APP_API}/topics/${topic_id}/subtopics`)
            .then(res => res.json())
            .then(data => {
                setSubtopics(data);
                setIsLoaded(true);
            })
            .catch(err => {
                setSubtopics([]);
                setIsLoaded(false);
                console.log(err);
            })
    }, [])

    const [title, setTitle] = useState("");
    const [err, setErr] = useState("");

    const [topic, setTopic] = useState({});

    const handleSubmit = async (e) => {
        e.preventDefault();
        if (title === "" || title.length <= 3) {
            setErr("Enter a valid subtopic title.")
        } else {
            if (Object.keys(topic).length === 0) {
                fetch(`${process.env.REACT_APP_API}/topics/${topic_id}`)
                    .then(res => res.json())
                    .then(_topic => {
                        fetch(`${process.env.REACT_APP_API}/subtopics`, {
                            method: "POST",
                            headers: {
                                'Authorization': `Bearer ${userToken}`,
                                'Content-Type': 'application/json'
                            },
                            body: JSON.stringify({
                                "title": title,
                                "topics": [_topic]
                            })
                        }).then(res => res.json()).then(data => {
                            setSubtopics(sbtps => [...sbtps, data]);
                            setTopic(_topic);
                            setErr("");
                            setShowModal(false);
                        }).catch(err => {
                            setErr("Oops! some error occured :(");
                            console.log(err);
                        })
                    })
            } else {
                fetch(`${process.env.REACT_APP_API}/subtopics`, {
                    method: "POST",
                    headers: {
                        'Authorization': `Bearer ${userToken}`,
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({
                        "title": title,
                        "topics": [topic]
                    })
                }).then(res => res.json()).then(data => {
                    setSubtopics(sbtps => [...sbtps, data]);
                    setTopic(topic);
                    setErr("");
                    setShowModal(false);
                }).catch(err => {
                    setErr("Oops! some error occured :(");
                    console.log(err);
                })
            }
        }
    }

    const handleDelete = e => {
        const cnfrm = window.confirm("Sure want to delete?");
        if (cnfrm) {
            let ind = e.target.dataset.index;
            let id = subtopics[ind].id;
            setIsLoaded(false);
            fetch(`${process.env.REACT_APP_API}/subtopics/${id}`, {
                method: "DELETE",
                headers: {
                    'Authorization': `Bearer ${userToken}`
                }
            }).then(res => res.json()).then(data => {
                setSubtopics(sbtps => sbtps.filter(item => item.id !== id));
                setIsLoaded(true);
            }).catch(err => {
                alert("Oops! some error occured. Try again");
                console.log(err);
                setIsLoaded(true);
            })
        }
    }
    const addForm = (
        <Modal show={showModal} onHide={() => setShowModal(false)} size="lg" centered>
            <Modal.Header closeButton>
                <Modal.Title>Add Subtopic</Modal.Title>
            </Modal.Header>

            <Modal.Body>
                {err !== "" ? <Alert variant="danger">{err}</Alert> : null}
                <Form onSubmit={handleSubmit}>
                    <Form.Group>
                        <Form.Label>Title</Form.Label>
                        <Form.Control type="text" required onChange={e => setTitle(e.target.value)} />
                    </Form.Group>
                    <Button variant="primary" type="submit">Add</Button>
                </Form>
            </Modal.Body >

        </Modal >
    );
    return (
        <Loader loaded={isLoaded}>
            <div className="d-flex justify-content-start flex-wrap">
                {addForm}
                {userToken !== "null" && topic_id === userTopicId ? <div className="add-resource-btn" onClick={() => setShowModal(true)}><FA name="plus" /></div> : null}
                {subtopics.map((st, index) =>
                    <div className="resource-card" key={st.id}>
                        {userToken !== "null" && topic_id === parseInt(userTopicId) ? <div className="action-btns">
                            <FA name="arrow-up" />
                            <FA name="arrow-down" />
                            <FA name="trash" data-index={index} onClick={handleDelete} />
                        </div> : null}
                        <Link className="custom-card" to={`/topics/${topic_id}/subtopics/${st.id}/notes`}>
                            {st.title}
                        </Link>
                    </div>
                )}
            </div>
        </Loader>
    );
}

export default SubtopicsList;