import React, { useContext } from 'react';
import { Navbar, Nav, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { UserContext } from '../contexts/UserContext';

function Navigation() {
    const { userToken, setUserToken, setUserTopicId, setUserCompanyId } = useContext(UserContext);

    const handleLogout = () => {
        localStorage.setItem('matesfeed-user', "null");
        localStorage.setItem('matesfeed-user-topic-id', "-1");
        localStorage.setItem('matesfeed-user-company-id', "-1");

        setUserTopicId("-1");
        setUserCompanyId("-1");
        setUserToken("null");
    }
    const logoutBtn = <Button variant="warning" onClick={handleLogout}>Logout</Button>;

    return (
        <Navbar bg="dark" variant="dark">
            <Navbar.Brand as={Link} to="/">Matesfeed</Navbar.Brand>
            <Nav className="ml-auto">
                <Nav.Link as={Link} to="/">Home</Nav.Link>
                <Nav.Link as={Link} to="/companies">Companies</Nav.Link>
                <Nav.Link as={Link} to="/topics">Topics</Nav.Link>

                {userToken === "null" ? <Nav.Link as={Link} to="/login">Login</Nav.Link> : logoutBtn}
            </Nav>
        </Navbar>
    );
}

export default Navigation;