import React, { useState, useEffect, useContext } from 'react';
import { Alert, Button, Card, Form, Modal, Nav } from 'react-bootstrap';
import { UserContext } from '../../contexts/UserContext';
import FA from 'react-fontawesome';
import Loader from 'react-loader';

function Extras(props) {
    const [subtopic, setSubtopic] = useState(props.subtopic);

    const [extras, setExtras] = useState([]);
    const { userToken, userTopicId } = useContext(UserContext);
    const [url, setUrl] = useState("");
    const [showModal, setShowModal] = useState(false);
    const [err, setErr] = useState("");
    const [newExtraTitle, setNewExtraTitle] = useState("");
    const [newExtraDescription, setNewExtraDescription] = useState("");
    const [urlLoaded, setUrlLoaded] = useState(false);
    const [isloaded, setIsLoaded] = useState(true);

    useEffect(() => {
        setIsLoaded(false);
        if (Object.keys(props.subtopic).length === 0 && userTopicId === parseInt(props.topic_id) && userToken !== "null") {
            fetch(`${process.env.REACT_APP_API}/subtopics/${props.id}`)
                .then(res => res.json())
                .then(data => setSubtopic(data));
        }
        fetch(`${process.env.REACT_APP_API}/subtopics/${props.id}/extras`)
            .then(res => res.json())
            .then(data => {
                setExtras(data);
                setIsLoaded(true);
            })
            .catch(err => {
                setExtras([]);
                setIsLoaded(true);
                console.log(err);
                alert("Oops! something went wrong");
            })
    }, []);

    const handleSubmit = e => {
        e.preventDefault();
        if (url === "" || url.length <= 5) setErr("enter a valid url");
        else if (!urlLoaded) {
            setIsLoaded(false);
            fetch(`https://api.microlink.io?url=${encodeURI(url)}`)
                .then(res => res.json())
                .then(data => {
                    setNewExtraTitle(data.data.title);
                    setNewExtraDescription(data.data.description);
                    setIsLoaded(true);
                    setUrlLoaded(true);
                }).catch(err => {
                    setErr("Oops! something went wrong!");
                    console.log(err);
                    setIsLoaded(true);
                })
        } else {
            setIsLoaded(false);
            fetch(`${process.env.REACT_APP_API}/extras`, {
                method: "POST",
                headers: {
                    'Authorization': `Bearer ${userToken}`,
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    "title": newExtraTitle,
                    "link": url,
                    "subtopics": [subtopic],
                    "description": newExtraDescription
                })
            }).then(res => res.json()).then(data => {
                setNewExtraTitle("");
                setNewExtraDescription("");
                setUrl("");
                setShowModal(false);
                setUrlLoaded(false);
                setIsLoaded(true);
                setErr("");
                setExtras(extras => [...extras, data])
            }).catch(err => {
                console.log(err);
                setIsLoaded(true);
                setErr("Oops! something went wrong :(");
            })

        }
    }
    const handleDelete = e => {
        const cnfrm = window.confirm("Sure want to delete?");
        if (cnfrm) {
            let ind = e.target.dataset.index;
            let id = extras[ind].id;
            setIsLoaded(false);
            fetch(`${process.env.REACT_APP_API}/extras/${id}`, {
                method: "DELETE",
                headers: {
                    'Authorization': `Bearer ${userToken}`
                }
            }).then(res => res.json()).then(data => {
                setExtras(_extras => _extras.filter(item => item.id !== id));
                setIsLoaded(true);
            }).catch(err => {
                alert("Oops! some error occured. Try again");
                console.log(err);
                setIsLoaded(true);
            })
        }
    }
    const addForm = (<Modal centered show={showModal} onHide={() => setShowModal(false)}>
        <Modal.Header closeButton>
            <Modal.Title>Add Extra</Modal.Title>
        </Modal.Header>
        <Modal.Body>
            {err !== "" ? <Alert variant="danger">{err}</Alert> : null}

            <Loader loaded={isloaded}>
                <Form onSubmit={handleSubmit}>
                    <Form.Group>
                        <Form.Label>Add url of the Extra</Form.Label>
                        <Form.Control type="url" required value={url} onChange={e => setUrl(e.target.value)} />
                    </Form.Group>
                    {urlLoaded ? <div>
                        <Form.Group>
                            <Form.Label>Title</Form.Label>
                            <Form.Control type="text" required value={newExtraTitle} onChange={e => setNewExtraTitle(e.target.value)} />
                        </Form.Group>
                        <Form.Group>
                            <Form.Label>Description</Form.Label>
                            <Form.Control type="text" required value={newExtraDescription} onChange={e => setNewExtraDescription(e.target.value)} />
                        </Form.Group>
                    </div> : null}
                    <Button type="submit" variant="primary">Add</Button>
                </Form>
            </Loader>
        </Modal.Body>
    </Modal>);

    return (
        <Loader loaded={isloaded}>
            <div className="container">
                {userToken !== "null" && userTopicId === parseInt(props.topic_id) ? <div className="add-resource-btn" onClick={() => setShowModal(true)}><FA name="plus" /></div> : null}
                {addForm}
                {extras.map((ex, index) =>
                    <Card key={ex.id} className="resource-card">
                        {userToken !== "null" && userTopicId === parseInt(props.topic_id) ? <div className="action-btns">
                            {/* <FA name="arrow-up" />
                            <FA name="arrow-down" /> */}
                            <FA name="trash" data-index={index} onClick={handleDelete} />
                        </div> : null}
                        <Card.Body as={Nav.Link} href={ex.link} target="_blank">
                            <Card.Title>{ex.title}</Card.Title>
                            <Card.Text>{ex.description}</Card.Text>
                        </Card.Body>
                    </Card>
                )}
            </div>
        </Loader>
    );
}

export default Extras;