import React, { useEffect, useState } from 'react';
import { Jumbotron, Button, Card } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { CircularProgressbar } from 'react-circular-progressbar';
import Loader from 'react-loader';

function Company(props) {

    const [company, setCompany] = useState({});
    const [isLoaded, setIsLoaded] = useState(true);
    useEffect(() => {
        setIsLoaded(false);
        fetch(`${process.env.REACT_APP_API}/get-company/${props.match.params.id}`)
            .then(res => res.json())
            .then(data => {
                setCompany(data);
                setIsLoaded(true);
            })
            .catch(err => {
                setCompany({});
                setIsLoaded(true);
                alert("Oops! something went wrong");
                console.log(err);
            })
    }, [])

    return (
        <Loader loaded={isLoaded}>
            <div>
                <Jumbotron className="row">
                    <div className="col-md-6">

                        <h1>{company.title}</h1>
                        <p>{company.description}</p>
                        <p>
                            <a href={company.link} className="btn btn-primary stretched-link" target="_blank">Learn More</a>
                        </p>

                    </div>
                    <div className="col-md-6 mt-md-0 mt-xs-2 d-flex justify-content-center flex-wrap float-right">
                        <div className="px-5">
                            <h4>Rating</h4>
                            <CircularProgressbar value={company.rating} maxValue={10} text={company.rating} />
                        </div>
                        <div className="px-5">
                            <h4>Internships</h4>
                            <CircularProgressbar value={company.internships} text={`${company.internships}%`} />
                        </div>
                        <div className="px-5">
                            <h4>Placements</h4>
                            <CircularProgressbar value={company.placements} text={`${company.placements}%`} />
                        </div>
                    </div>
                </Jumbotron>
                <br />
                <div className="d-flex justify-content-center flex-wrap">
                    <Card className="mx-3" style={{ width: '25rem' }}>
                        <Card.Body>
                            <Card.Title>Topics</Card.Title>
                            <Card.Text>
                                Covers different topics which were asked in the interviews and exams.
                        </Card.Text>
                            <Button variant="secondary" as={Link} to={`/companies/${company.id}/topics`}>Check here</Button>
                        </Card.Body>
                    </Card>
                    <Card className="mx-3" style={{ width: '25rem' }}>
                        <Card.Body>
                            <Card.Title>Practice Problems</Card.Title>
                            <Card.Text>
                                Provides rich and nicely currated problems to test your concepts.
                        </Card.Text>
                            <Button variant="secondary" as={Link} to={`/companies/${company.id}/problems`}>Check here</Button>
                        </Card.Body>
                    </Card>
                </div>
            </div>
        </Loader>
    );
}

export default Company;