import React, { useState, useEffect, useContext } from 'react';
import { Alert, Badge, Button, Card, Form, Modal, Nav } from 'react-bootstrap';
import { UserContext } from '../../contexts/UserContext';
import FA from 'react-fontawesome';
import Loader from 'react-loader';
import MultiSelect from 'react-multi-select-component';

function Problems(props) {
    const { userToken, userTopicId } = useContext(UserContext);

    const [subtopic, setSubtopic] = useState(props.subtopic);
    const [companies, setCompanies] = useState([]);
    const [selectedCompanies, setSelectedCompanies] = useState([]);
    const [companyOptions, setCompanyOptions] = useState([]);
    const [problems, setproblems] = useState([]);

    const [link, setLink] = useState("");
    const [title, setTitle] = useState("");
    const [difficulty, setDifficulty] = useState(0);

    const [isLoaded, setIsLoaded] = useState(true);
    const [showModal, setShowModal] = useState(false);
    const [err, setErr] = useState("");

    useEffect(() => {

        setIsLoaded(false);

        if (userToken !== "null") {
            fetch(`${process.env.REACT_APP_API}/companies`)
                .then(res => res.json())
                .then(data => {
                    setCompanies(data);
                    setCompanyOptions(data.map((c, ind) => {
                        return { "label": c.title, "value": ind };
                    }))
                })
        }

        if (Object.keys(props.subtopic).length === 0 && userToken !== "null") {
            fetch(`${process.env.REACT_APP_API}/subtopics/${props.id}`)
                .then(res => res.json())
                .then(data => setSubtopic(data));
        }

        fetch(`${process.env.REACT_APP_API}/subtopics/${props.id}/problems`)
            .then(res => res.json())
            .then(data => {
                setproblems(data);
                setIsLoaded(true);
            })
            .catch(err => {
                setproblems([]);
                setIsLoaded(true);
                alert("Oops! something went wrong");
                console.log(err);
            })
    }, []);

    const badges = {
        0: <Badge variant="success" >Easy</Badge>,
        1: <Badge variant="primary" >Medium</Badge>,
        2: <Badge variant="danger" >Hard</Badge>
    }

    const handleSubmit = e => {
        e.preventDefault();
        setIsLoaded(false);
        fetch(`${process.env.REACT_APP_API}/problems`, {
            method: "POST",
            headers: {
                'Authorization': `Bearer ${userToken}`,
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                "title": title,
                "link": link,
                "subtopics": [subtopic],
                "difficulty": difficulty,
                "companies": selectedCompanies.length > 0 ? selectedCompanies.map(sc => companies[sc.value]) : []
            })
        }).then(res => res.json()).then(data => {
            setIsLoaded(true);
            setproblems(prblms => [...prblms, data]);
            setErr("");
            setTitle("");
            setLink("");
            setDifficulty(0);
            setShowModal(false);
        }).catch(err => {
            setErr("Oops! something went wrong :(");
            console.log(err);
        })
    }

    const handleDelete = e => {
        const cnfrm = window.confirm("Sure want to delete?");
        if (cnfrm) {

            let ind = e.target.dataset.index;
            let id = problems[ind].id;
            setIsLoaded(false);
            fetch(`${process.env.REACT_APP_API}/problems/${id}`, {
                method: "DELETE",
                headers: {
                    'Authorization': `Bearer ${userToken}`
                }
            }).then(res => res.json()).then(data => {
                setproblems(prblms => prblms.filter(item => item.id !== id));
                setIsLoaded(true);
            }).catch(err => {
                alert("Oops! some error occured. Try again");
                console.log(err);
                setIsLoaded(true);
            })
        }
    }

    const addForm = (
        <Modal show={showModal} onHide={() => setShowModal(false)}>
            <Modal.Header>
                <Modal.Title>
                    Add Problem
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form onSubmit={handleSubmit}>
                    {err !== "" ? <Alert variant="danger">{err}</Alert> : null}
                    <Form.Group>
                        <Form.Label>Url</Form.Label>
                        <Form.Control type="url" value={link} onChange={e => setLink(e.target.value)} required />
                    </Form.Group>
                    <Form.Group>
                        <Form.Label>Title</Form.Label>
                        <Form.Control type="text" value={title} onChange={e => setTitle(e.target.value)} required />
                    </Form.Group>

                    <Form.Group>
                        <Form.Label>Difficulty</Form.Label>
                        <Form.Control as="select" defaultValue={0} onChange={e => setDifficulty(e.target.value)} >
                            <option value={0}>Easy</option>
                            <option value={1}>Medium</option>
                            <option value={2}>Hard</option>
                        </Form.Control>
                    </Form.Group>

                    <Form.Group>
                        <Form.Label>Companies</Form.Label>
                        <MultiSelect options={companyOptions} value={selectedCompanies} onChange={setSelectedCompanies} labelledBy={"Select"} />
                    </Form.Group>
                    <Button type="submit" variant="primary">Add</Button>
                </Form>
            </Modal.Body>
        </Modal>
    );
    return (
        <Loader loaded={isLoaded}>
            <div className="container">
                {userToken !== "null" && userTopicId === parseInt(props.topic_id) ? <div className="add-resource-btn" onClick={() => setShowModal(true)}><FA name="plus" /></div> : null}
                {addForm}
                {problems.map((prb, index) =>
                    <Card key={prb.id} className="resource-card">
                        {userToken !== "null" && userTopicId === parseInt(props.topic_id) ? <div className="action-btns">
                            {/* <FA name="arrow-up" />
                            <FA name="arrow-down" /> */}
                            <FA name="trash" data-index={index} onClick={handleDelete} />
                        </div> : null}
                        <Card.Body as={Nav.Link} href={prb.link} target="_blank">
                            <Card.Text>{prb.title}
                                <span className="float-right difficulty-badge">
                                    {badges[prb.difficulty]}
                                </span>

                            </Card.Text>
                        </Card.Body>
                    </Card>
                )}
            </div>
        </Loader>
    );
}

export default Problems;