import React, { useEffect, useState, useContext } from 'react';
import { Link } from 'react-router-dom';
import { UserContext } from '../../contexts/UserContext';
import FA from 'react-fontawesome';
import { Form, Modal, Button, Alert } from 'react-bootstrap';
import Select from 'react-select';
import Loader from 'react-loader';

function CompanyTopicsList(props) {

    const company_id = props.match.params.company_id;
    const [company, setCompany] = useState({});
    const [topics, setTopics] = useState([]);
    const [options, setOptions] = useState([]);
    const [selectedTopics, setSelectedTopics] = useState([]);
    const { userToken, userCompanyId } = useContext(UserContext);
    const [isLoaded, setIsLoaded] = useState(true);
    const [err, setErr] = useState("");
    const [showModal, setShowModal] = useState(false);

    useEffect(() => {
        setIsLoaded(false);
        fetch(`${process.env.REACT_APP_API}/companies/${company_id}/topics`)
            .then(res => res.json())
            .then(data => {
                setCompany(data);
                setTopics(data.topics);
                setSelectedTopics(data.topics.map(item => {
                    return {
                        "label": item.title,
                        "value": item
                    }
                }));
                setIsLoaded(true);
            })
            .catch(err => {
                setCompany({});
                setTopics([]);
                setSelectedTopics([]);
                setIsLoaded(true);
                alert("Oops! something went wrong");
                console.log(err);
            })

        if (userToken !== "null" && userCompanyId === parseInt(company_id)) {
            fetch(`${process.env.REACT_APP_API}/topics`)
                .then(res => res.json())
                .then(data => {
                    let _options = data.map(item => {
                        return {
                            "label": item.title,
                            "value": item
                        }
                    })
                    setOptions(_options);
                });
        }


    }, []);

    const handleSubmit = e => {
        e.preventDefault();
        setIsLoaded(false);
        company.topics = selectedTopics.map(item => item.value);
        fetch(`${process.env.REACT_APP_API}/companies/${company_id}`, {
            method: "PUT",
            headers: {
                'Authorization': `Bearer ${userToken}`,
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(company)
        }).then(res => res.json()).then(data => {
            setCompany(data);
            setIsLoaded(true);
            setErr("");
            setShowModal(false);
            setTopics(data.topics);
        }).catch(err => {
            setErr("Oops! some error occured :(");
            console.log(err);
            setIsLoaded(true);
        })
    }
    const addForm = (
        <Modal show={showModal} onHide={() => setShowModal(false)} >
            <Modal.Header>
                <Modal.Title>Add Topics</Modal.Title>
            </Modal.Header>

            <Modal.Body>
                <Form onSubmit={handleSubmit}>
                    {err !== "" ? <Alert variant="danger">{err}</Alert> : null}
                    <Form.Group>
                        <Form.Label>Select Topics</Form.Label>
                        <Select isMulti options={options} value={selectedTopics} onChange={setSelectedTopics} />
                    </Form.Group>
                    <Button type="submit" variant="primary">Add topics</Button>
                </Form>
            </Modal.Body>

        </Modal>
    );
    return (
        <Loader loaded={isLoaded}>
            <div className="d-flex justify-content-start flex-wrap">
                {userToken !== "null" && userCompanyId === parseInt(company_id) ? <div className="add-resource-btn" onClick={() => setShowModal(true)}><FA name="plus" /></div> : null}
                {userToken !== "null" && userCompanyId === parseInt(company_id) ? addForm : null}
                {topics.map(t => <Link key={t.id} className="custom-card" to={`/topics/${t.id}/subtopics`}>{t.title}</Link>)}
            </div>
        </Loader>
    );
}

export default CompanyTopicsList;