import React, { useContext, useEffect } from 'react';
import { Route, BrowserRouter as Router } from 'react-router-dom';
import Login from './components/Login';
import Home from './components/Home';
import Navigation from './components/Navigation';
import CompaniesList from './components/company/CompaniesList';
import TopicsList from './components/topic/TopicsList';
import Company from './components/company/Company';
import CompanyTopicsList from './components/company/CompanyTopicsList';
import SubtopicsList from './components/topic/SubtopicsList';
import Subtopic from './components/topic/Subtopic';
import CompanyProblemsList from './components/company/CompanyProblemsList';
import { UserContext } from './contexts/UserContext';

function App() {

  const { setUserToken, setUserTopicId, setUserCompanyId } = useContext(UserContext);

  useEffect(() => {
    const tkn = localStorage.getItem('matesfeed-user');
    const tpcId = localStorage.getItem('matesfeed-user-topic-id');
    const cmpny = localStorage.getItem('matesfeed-user-company-id');

    if (tkn === "null") setUserToken("null");
    else setUserToken(tkn);

    if (tpcId === "null") setUserTopicId(-1);
    else setUserTopicId(parseInt(tpcId));

    if (cmpny === "null") setUserCompanyId(-1);
    else setUserCompanyId(parseInt(cmpny));

  }, [])

  return (
    <Router basename="/matesfeed">
      <div className="App">
        <Navigation />
        <Route path="/" exact component={Home} />
        <Route path="/companies" exact component={CompaniesList} />
        <Route path="/companies/:id" exact component={Company} />
        <Route path="/companies/:company_id/topics" exact component={CompanyTopicsList} />
        <Route path="/companies/:id/problems" exact component={CompanyProblemsList} />

        <Route path="/topics" exact component={TopicsList} />
        <Route path="/topics/:topic_id/subtopics" exact component={SubtopicsList} />
        <Route path="/topics/:topic_id/subtopics/:id/:cat" exact component={Subtopic} />

        <Route path="/login" exact component={Login} />
      </div>
    </Router>
  );
}

export default App;
