import React, { useState, useEffect } from 'react';
import { Badge, Card, Nav } from 'react-bootstrap';
import Loader from 'react-loader';

function CompanyProblemsList(props) {

    const [problems, setproblems] = useState([]);
    const [isLoaded, setIsLoaded] = useState(true);

    useEffect(() => {
        setIsLoaded(false);
        fetch(`${process.env.REACT_APP_API}/companies/${props.match.params.id}/problems`)
            .then(res => res.json())
            .then(data => {
                setproblems(data);
                setIsLoaded(true);
            })
            .catch(err => {
                setIsLoaded(true);
                setproblems([]);
                console.log(err);
                alert("Oops! something went wrong");
            })
    }, []);

    const badges = {
        0: <Badge variant="success" >Easy</Badge>,
        1: <Badge variant="primary" >Medium</Badge>,
        2: <Badge variant="danger" >Hard</Badge>
    }
    return (
        <Loader loaded={isLoaded}>
            <div className="container">
                {problems.map(prb =>
                    <Card className="p-1 my-1 stretched-link custom-subtopic-cat-card" as={Nav.Link} href={prb.link} target="_blank">
                        <Card.Body>
                            <Card.Text>{prb.title} <div className="float-right">{badges[prb.difficulty]}</div></Card.Text>
                        </Card.Body>
                    </Card>
                )}
            </div>
        </Loader>
    );
}

export default CompanyProblemsList;