import React, { useState, useEffect, useContext } from 'react';
import { Alert, Button, Card, Form, Modal, Nav } from 'react-bootstrap';
import { UserContext } from '../../contexts/UserContext';
import FA from 'react-fontawesome';
import Loader from 'react-loader';

function Notes(props) {
    const [subtopic, setSubtopic] = useState(props.subtopic);
    const { userToken, userTopicId } = useContext(UserContext);
    const [notes, setNotes] = useState([]);
    const [url, setUrl] = useState("");
    const [showModal, setShowModal] = useState(false);
    const [err, setErr] = useState("");
    const [newNoteTitle, setNewNoteTitle] = useState("");
    const [newNoteDescription, setNewNoteDescription] = useState("");
    const [urlLoaded, setUrlLoaded] = useState(false);
    const [isloaded, setIsLoaded] = useState(true);

    const handleSubmit = e => {
        e.preventDefault();
        if (url === "" || url.length <= 5) setErr("enter a valid url");
        else if (!urlLoaded) {
            setIsLoaded(false);
            fetch(`https://api.microlink.io?url=${encodeURI(url)}`)
                .then(res => res.json())
                .then(data => {
                    setNewNoteTitle(data.data.title);
                    setNewNoteDescription(data.data.description);
                    setIsLoaded(true);
                    setUrlLoaded(true);
                }).catch(err => {
                    setErr("Oops! something went wrong!");
                    console.log(err);
                    setIsLoaded(true);
                })
        } else {
            setIsLoaded(false);
            fetch(`${process.env.REACT_APP_API}/notes`, {
                method: "POST",
                headers: {
                    'Authorization': `Bearer ${userToken}`,
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    "title": newNoteTitle,
                    "link": url,
                    "subtopics": [subtopic],
                    "description": newNoteDescription
                })
            }).then(res => res.json()).then(data => {
                setNewNoteTitle("");
                setNewNoteDescription("");
                setUrl("");
                setShowModal(false);
                setUrlLoaded(false);
                setIsLoaded(true);
                setErr("");
                setNotes(notes => [...notes, data])
            }).catch(err => {
                console.log(err);
                setIsLoaded(true);
                setErr("Oops! something went wrong :(");
            })

        }
    }
    useEffect(() => {
        setIsLoaded(false);
        if (Object.keys(props.subtopic).length === 0) {
            fetch(`${process.env.REACT_APP_API}/subtopics/${props.id}`)
                .then(res => res.json())
                .then(data => setSubtopic(data));
        }
        fetch(`${process.env.REACT_APP_API}/subtopics/${props.id}/notes`)
            .then(res => res.json())
            .then(data => {
                setNotes(data);
                setIsLoaded(true);
            }).catch(err => {
                setNotes([]);
                setIsLoaded(true);
                alert("Oops! something error occured :(")
            })
    }, [])
    const handleDelete = e => {
        const cnfrm = window.confirm("Sure want to delete?");
        if (cnfrm) {
            let ind = e.target.dataset.index;
            let id = notes[ind].id;
            setIsLoaded(false);
            fetch(`${process.env.REACT_APP_API}/notes/${id}`, {
                method: "DELETE",
                headers: {
                    'Authorization': `Bearer ${userToken}`
                }
            }).then(res => res.json()).then(data => {
                setNotes(nts => nts.filter(item => item.id !== id));
                setIsLoaded(true);
            }).catch(err => {
                alert("Oops! some error occured. Try again");
                console.log(err);
                setIsLoaded(true);
            })
        }
    }
    const addForm = (<Modal centered show={showModal} onHide={() => setShowModal(false)}>
        <Modal.Header closeButton>
            <Modal.Title>Add Note</Modal.Title>
        </Modal.Header>
        <Modal.Body>
            {err !== "" ? <Alert variant="danger">{err}</Alert> : null}

            <Form onSubmit={handleSubmit}>
                <Form.Group>
                    <Form.Label>Add url of the note</Form.Label>
                    <Form.Control type="url" required value={url} onChange={e => setUrl(e.target.value)} />
                </Form.Group>
                {urlLoaded ? <div>
                    <Form.Group>
                        <Form.Label>Title</Form.Label>
                        <Form.Control type="text" required value={newNoteTitle} onChange={e => setNewNoteTitle(e.target.value)} />
                    </Form.Group>
                    <Form.Group>
                        <Form.Label>Description</Form.Label>
                        <Form.Control type="text" required value={newNoteDescription} onChange={e => setNewNoteDescription(e.target.value)} />
                    </Form.Group>
                </div> : null}
                <Button type="submit" variant="primary">Add</Button>
            </Form>
        </Modal.Body>
    </Modal>);
    return (
        <Loader loaded={isloaded}>
            <div className="container">
                {userToken !== "null" && userTopicId === parseInt(props.topic_id) ? <div className="add-resource-btn" onClick={() => setShowModal(true)}><FA name="plus" /></div> : null}
                {addForm}
                {notes.map((n, index) =>
                    <Card key={n.id} className="resource-card">
                        {userToken !== "null" && userTopicId === parseInt(props.topic_id) ? <div className="action-btns">
                            {/* <FA name="arrow-up" />
                            <FA name="arrow-down" /> */}
                            <FA name="trash" data-index={index} onClick={handleDelete} />
                        </div> : null}
                        <Card.Body as={Nav.Link} href={n.link} target="_blank">
                            <Card.Title>{n.title}</Card.Title>
                            <Card.Text>{n.description}</Card.Text>
                        </Card.Body>
                    </Card>
                )}
            </div></Loader>);
}

export default Notes;